function openLinkInBrowser(url: string) {
  Linking.canOpenURL(url).then((canOpen) => canOpen && Linking.openURL(url))
}

export const ProfileScreen: FC<DemoTabScreenProps<"ProfileScreen">> = observer(function ProfileScreen() {
  // Pull in one of our MST stores
  // const { someStore, anotherStore } = useStores()
  const {
    authenticationStore: { logout, userFullName, userEmail, userRole, userLogoutInprogress },
  } = useStores()

  // Pull in navigation via hook
  // const navigation = useNavigation()
  return (
    <Screen preset="scroll" safeAreaEdges={["top"]} contentContainerStyle={$container}>
      <Text style={$title} preset="heading" tx="profileScreen.title" />
      <View style={$itemsContainer}>
        <ListItem
            LeftComponent={
              <View style={$item}>
                <Text preset="bold">Full Name</Text>
                <Text>{String(userFullName)}</Text>
              </View>
            }
          />
          <ListItem
            LeftComponent={
              <View style={$item}>
                <Text preset="bold">Email</Text>
                <Text>{String(userEmail)}</Text>
              </View>
            }
          />
          <ListItem
            LeftComponent={
              <View style={$item}>
                <Text preset="bold">Role</Text>
                <Text>{String(userRole)}</Text>
              </View>
            }
          />
          <ListItem
            LeftComponent={
              <View style={$item}>
                <Text preset="bold">App Version</Text>
                <Text>0.0.5 (2023-06-27 17:21)</Text>
              </View>
            }
          />
      </View>
      <View style={$buttonContainer}>
        {!userLogoutInprogress && <Button style={$button} tx="common.logOut" onPress={logout} />}
        {userLogoutInprogress && <Button disabled style={$button} text="inprogress..." onPress={logout} />}
      </View>
    </Screen>
  )
})

const $container: ViewStyle = {
  paddingTop: spacing.lg + spacing.xl,
  paddingBottom: spacing.xxl,
  paddingHorizontal: spacing.lg,
}

const $title: TextStyle = {
  marginBottom: spacing.xxl,
}

const $reportBugsLink: TextStyle = {
  color: colors.tint,
  marginBottom: spacing.lg,
  alignSelf: isRTL ? "flex-start" : "flex-end",
}

const $item: ViewStyle = {
  marginBottom: spacing.md,
}

const $itemsContainer: ViewStyle = {
  marginBottom: spacing.xl,
}

const $button: ViewStyle = {
  marginBottom: spacing.xs,
}

const $buttonContainer: ViewStyle = {
  marginBottom: spacing.md,
}

const $hint: TextStyle = {
  color: colors.palette.neutral600,
  fontSize: 12,
  lineHeight: 15,
  paddingBottom: spacing.lg,
}
